import { redirect } from "next/navigation";
import { getServerApiBase } from "@/lib/api";
import { buildServerCookieHeader } from "@/lib/server/cookies";

export type ServerMe = {
  id: string;
  name: string;
  role: "admin" | "accountant" | "user";
};

export async function fetchServerMe(cookieHeader?: string): Promise<ServerMe> {
  const header = cookieHeader ?? (await buildServerCookieHeader());
  if (!header) redirect("/login");
  const res = await fetch(`${getServerApiBase()}/me`, {
    headers: { Cookie: header },
    cache: "no-store",
  });
  if (res.status === 401) redirect("/login");
  if (!res.ok) throw new Error("Failed to load profile");
  return res.json();
}
